<template>
  <div>
    <div ref="point" :class="classes" :style="styles">
      <slot/>
    </div>
    <div v-show="slot" :style="slotStyle"></div>
  </div>
</template>
<script>
  const prefixCls = 'ea-affix';

  function getScroll(target, top) {
    const prop = top ? 'pageYOffset' : 'pageXOffset';
    const method = top ? 'scrollTop' : 'scrollLeft';
    let ret = target[prop];
    if (typeof ret !== 'number') {
      ret = window.document.documentElement[method];
    }
    return ret;
  }

  function getOffset(element) {
    const rect = element.getBoundingClientRect();
    const scrollTop = getScroll(window, true);
    const scrollLeft = getScroll(window);
    const docEl = window.document.body;
    const clientTop = docEl.clientTop || 0;
    const clientLeft = docEl.clientLeft || 0;
    return {
      top: rect.top + scrollTop - clientTop,
      left: rect.left + scrollLeft - clientLeft,
    };
  }

  export default {
    name: 'EaAffix',
    components: {},
    props: {
      affix: {
        type: Boolean,
        default: true,
      },
      offsetTop: {
        type: [Number, Boolean],
        default: 0,
      },
      offsetBottom: {
        type: Number,
      },
      zIndex: {
        type: Number,
        default: 1000,
      },
    },
    watch: {
      affix(v) {
        if (!v && this.mAffix) {
          this.slot = false;
          this.slotStyle = {};
          this.mAffix = false;
          this.styles = {
            zIndex: this.zIndex,
          };
          this.$emit('change', false);
        }
      },
    },
    data() {
      return {
        mAffix: false,
        styles: {
          zIndex: this.zIndex,
        },
        slot: false,
        slotStyle: {},
      };
    },
    computed: {
      offsetType() {
        let type = 'top';
        if (this.offsetBottom >= 0) {
          type = 'bottom';
        }
        return type;
      },
      classes() {
        return [
          {
            [`${prefixCls}`]: this.mAffix,
          },
        ];
      },
    },
    methods: {
      handleScroll() {
        if (!this.affix) return false;
        const affix = this.mAffix;
        const scrollTop = getScroll(window, true);
        const elOffset = getOffset(this.$el);
        const windowHeight = window.innerHeight;
        const elHeight = this.$el.getElementsByTagName('div')[0].offsetHeight;

        // Fixed Top
        if ((elOffset.top - this.offsetTop) < scrollTop && this.offsetType === 'top' && !affix) {
          this.mAffix = true;
          this.slotStyle = {
            width: this.$refs.point.clientWidth + 'px',
            height: this.$refs.point.clientHeight + 'px',
          };
          this.slot = true;
          this.styles = {
            top: `${this.offsetTop}px`,
            left: `${elOffset.left}px`,
            width: `${this.$el.offsetWidth}px`,
            zIndex: this.zIndex,
          };
          this.$emit('change', true);
        } else if ((elOffset.top - this.offsetTop) > scrollTop && this.offsetType === 'top' && affix) {
          this.slot = false;
          this.slotStyle = {};
          this.mAffix = false;
          this.styles = {
            zIndex: this.zIndex,
          };
          this.$emit('change', false);
        }
        // Fixed Bottom
        if ((elOffset.top + this.offsetBottom + elHeight) > (scrollTop + windowHeight) && this.offsetType === 'bottom' && !affix) {
          this.mAffix = true;
          this.styles = {
            bottom: `${this.offsetBottom}px`,
            left: `${elOffset.left}px`,
            width: `${this.$el.offsetWidth}px`,
            zIndex: this.zIndex,
          };
          this.$emit('change', true);
        } else if ((elOffset.top + this.offsetBottom + elHeight) < (scrollTop + windowHeight) && this.offsetType === 'bottom' && affix) {
          this.mAffix = false;
          this.styles = {
            zIndex: this.zIndex,
          };
          this.$emit('change', false);
        }

        if (this.styles.left && `${elOffset.left}px` !== this.styles.left) {
          this.styles.left = `${elOffset.left}px`;
        }
      },
    },
    created() {
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll, false);
      window.addEventListener('resize', this.handleScroll, false);
      this.$nextTick(() => {
        this.handleScroll();
      });
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.handleScroll, false);
      window.removeEventListener('resize', this.handleScroll, false);
    },
  };
</script>